.tilesWrap {
  padding: 0;
  list-style: none;
  text-align: center;

}

.tilesWrap li {
  display: inline-block;
  width: 20%;
  min-width: 200px;
  max-width: 300px;
  padding: 80px 20px 40px;
  position: relative;
  vertical-align: top;
  margin: 20px;
  font-family: 'helvetica', san-serif;
  min-height: 25vh;
  background: var(--pickled-bluewood-900);
  border: 1px solid #252727;
  text-align: left;
  height: 320px;
}

.tilesWrap li h2 {
  font-size: 114px;
  margin: 0;
  position: absolute;
  opacity: 0.2;
  top: 50px;
  right: 10px;
  transition: all 0.3s ease-in-out;
  color: #de945f;
}
.tilesWrap li h3 {
  font-size: 20px;
  color: white;
  margin-bottom: 5px;
}
.tilesWrap li p {
  font-size: 16px;
  line-height: 18px;
  color: #b7b7b7;
  margin-top: 5px;
}
.tilesWrap li button {
  background: transparent;
  border: 1px solid #b7b7b7;
  padding: 10px 30px;
  color: white;
  border-radius: 3px;
  position: relative;
  transition: all 0.3s ease-in-out;
  transform: translateY(-40px);
  opacity: 0;
  cursor: pointer;
  overflow: hidden;
}
.tilesWrap li button:before {
  content: '';
  position: absolute;
  height: 100%;
  width: 120%;
  background: #de945f;
  top: 0;
  opacity: 0;
  left: -140px;
  border-radius: 0 20px 20px 0;
  z-index: -1;
  transition: all 0.3s ease-in-out;
  
}
.tilesWrap li:hover button {
  transform: translateY(5px);
  opacity: 1;
}
.tilesWrap li button:hover {
  background-color: #de945f;
  color: #fff;
}
.tilesWrap li button:hover:before {
  left: 0;
  opacity: 1;
}
.tilesWrap li:hover h2 {
  top: 0px;
  opacity: 0.6;
}

.tilesWrap li:before {
  content: '';
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  z-index: -1;
  background: #fff;
  transform: skew(2deg, 2deg);
}
.tilesWrap li:after {
  content: '';
  position: absolute;
  width: 10%;
  height: 100%;
  left: 0;
  top: 0;
  background: rgba(255, 255, 255, 0.02);
}


h2{
    font-size: 4vw; /* Browsers that do not support "MIN () - MAX ()" and "Clamp ()" functions will take this value.*/
    font-size: max(30px, min(3vw, 30px)); /* Browsers that do not support the "clamp ()" function will take this value. */
    font-size: clamp(30px, 3vw, 30px);
}

