}

/* Navbar container styles */
.max-w-screen-xl {
  position: relative;
}

/* Adjust the padding to give space for the lines */
.max-w-screen-xl::before,
.max-w-screen-xl::after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  height: 1px;
  background-color: #e2e8f0; /* Adjust the color as needed */
  opacity: 0; /* Initially invisible */
  transition: opacity 0.3s ease;
}

/* Hover effect - show lines on hover */
.max-w-screen-xl:hover::before,
.max-w-screen-xl:hover::after {
  opacity: 1;
}

/* Line positions */
.max-w-screen-xl::before {
  top: 0;
}

.max-w-screen-xl::after {
  bottom: 0;
}

.card-container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  transition: all 200ms ease-in-out;
}

.card {
  align-self: flex-start;
  position: relative;
  width: 325px;
  min-width: 325px;
  margin: 1.25rem 0.75rem;
  background: white;
  transition: all 300ms ease-in-out;
}

.card .card-img {
  visibility: hidden;
  width: 100%;
  height: var(--card-img-height);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.card .card-info {
  position: relative;
  padding: 0.75rem 1.25rem;
  transition: all 200ms ease-in-out;
}

.card .card-info .card-about {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem 0;
  transition: all 200ms ease-in-out;
}

.card .card-info .card-about .card-tag {
  padding: 0.2rem 0.5rem;
  font-size: 12px;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 1px;
  background: #505f79;
  color: #fff;
}

.card .card-info .card-about .card-tag.tag-news {
  background: #36b37e;
}

.card .card-info .card-about .card-tag.tag-etc {
  background: #ffab00;
}

.card .card-info .card-about .card-tag.tag-press {
  background: #ff5630;
}

.card .card-info .card-title {
  z-index: 10;
  font-size: 1.5rem;
  padding-bottom: 0.75rem;
  transition: all 350ms ease-in-out;
}

.card .card-info .card-creator {
  padding-bottom: 0.75rem;
  transition: all 250ms ease-in-out;
}

.card:hover {
  cursor: pointer;
  box-shadow: 0px 15px 35px rgba(227, 252, 239, 0.1), 0px 5px 15px rgba(0, 0, 0, 0.07);
  transform: scale(1.025);
}


.card:hover .card-about,
.card:hover .card-creator {
  opacity: 0;
}

.card:hover .card-info {
  background-color: transparent;
}

.card:hover .card-title {
  color: #ebecf0;
  transform: translate(0, 40px);
}

.card:hover .card-img-hovered {
  --card-img-hovered-overlay: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.65));
  height: 100%;
}

.card .card-img-hovered {
  --card-img-hovered-overlay: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0));
  transition: all 350ms ease-in-out;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  width: 100%;
  position: absolute;
  height: var(--card-img-height);
  top: 0;
}

.news-container {
