.submenu {
  position: fixed;
  top: 70%;
  right: 80%;
  width: 200px;
  color: var(--pickled-bluewood-900);
  z-index: 10;
  border-top: solid 3px var(--pickled-bluewood-900);
  border-bottom: solid 3px var(--pickled-bluewood-900);
}

.submenu .active{
  color: #de945f;
  text-decoration: underline;
  opacity: 1;
}

.submenu ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.submenu ul li {
  text-decoration: none;
  color: var(--pickled-bluewood-900);
  transition: color 0.3s;
}

.submenu ul li:hover {
  background-color: #de945f;
  text-decoration: none;
  color: #fff;
  opacity: 1;
}
